@import url(https://fonts.googleapis.com/css?family=Noto+Sans+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#home {
  margin-top: 0;
  text-align: center;
}

#intro {
  margin-top: 12vh;
  display: inline-block;
  color: black;
}

#me {
  border-radius: 50%;
}

#intro > #name {
  color: #800020;
}

#logos {
  padding: 0;
  list-style: none;
  list-style-type: none;
  margin-top: 3vh;
}

#logos > li {
  display: inline;
}

.logo {
  width: 40px;
  margin: auto;
  margin: 0 1.8vw;
}

h1 {
  margin: 0;
}

/* Tooltip animations */

.tooltip {
  position: relative;
  display: inline-block;
}

#navbar {
  width: 100%;
  float: left;
  margin: 0 0 3em 0;
  padding: 0;
  padding-top: 2vh;
  padding-bottom: 2vh;
  padding-left: 1vw;
  list-style: none;
  border-bottom: 2px solid #800020;
  line-height: 2em;
  position: fixed;
  top: 0;
  z-index: 9999;
  overflow: hidden;
}

#navbar > #namenav {
  font-size: 20px;
  margin-right: 4vw;
}

#navbar li {
  float: left;
  margin-right: 2vw;
  border-bottom: none;
}

a {
  text-decoration: none;
  color: black;
}

a:visited {
  text-decoration: none;
  color: black;
}
#about {
  margin: 15vh 26vw 0vh 30vw;
  text-align: left;
}

#about::after {
  content: "";
  display: block;
  clear: both;
}

#title {
  color: #800020;
}

.job {
  color: #800020;
}

#about-me {
  float: left;
  margin-right: 5vw;
}

#skills {
  float: left;
  margin-left: 0;
}

#skills-logos {
  padding-left: 0;
  list-style: none;
  list-style-type: none;
  float: left;
  padding-right: 0;
  margin-bottom: 3vh;
}

#skills-logos-2 {
  padding-left: 0;
  list-style: none;
  list-style-type: none;
  float: left;
  padding-right: 0;
  margin-bottom: 3vh;
  width: 95%;
}

#skills-logos-3 {
  padding-left: 0;
  list-style: none;
  list-style-type: none;
  float: left;
  padding-right: 0;
  margin-bottom: 3vh;
  width: 100%;
}

#skills-logos > li {
  display: inline;
}

#skills-logos-2 > li {
  display: inline;
}

#skills-logos-3 > li {
  display: inline;
}

html {
  scroll-behavior: smooth;
}

#main {
  padding-top: calc(38px + 4vh);
  font-family: "Noto Sans SC", sans-serif;
  background-color: white;
}

